import React, { useState } from 'react';
import { Link } from 'react-router-dom';

const Dropdown = () => {
    return (
        <div className='flex flex-col p-2 space-y-4 bg-white rounded-xl shadow'>
            <button className='p-1'> Students </button>
            <button className='p-1'> Staff </button>
        </div>
    )
}

const Nav = () => {

    const [listOpen, setListOpen] = React.useState(false);

    return (
        <nav className='flex flex-row justify-between items-center bg-white py-4 px-10 fixed top-0 w-full z-10 h-20'>
                <button onClick={()=>setListOpen(false)} className='flex justify-start w-14'><svg className='w-7' fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16"/></svg></button>
                <div className='w-14'> <Link to="/"><img src={require('../images/scclogo.png').default} alt='scc logo' className='h-10' onClick={()=>setListOpen(false)}/></Link> </div>
                {listOpen?<Dropdown />:null}
                <div className='w-14'> <button onClick={()=>setListOpen(!listOpen)} className='font-medium text-base'>Sign in</button></div>
        </nav>
    )
}

export default Nav
